<template>
<div>
   <li>
    <input type="checkbox" name="checkbox" :id="id" v-bind:checked="done">
    <label :for="id">{{ label }}</label>
   </li>
</div>
</template>

<script>
import uniqueId from "lodash.uniqueid";
export default{
    props: {
        done: {default: false, type: Boolean},
        label: {required: false, type: String}
    },
    data (){
        return {
            isDone: this.done,
            id: uniqueId("todo-"),
        }
    }

}
</script>

<style>
input.checkbox{
    display: inline;
}

</style>